<template>
  <div>
    <CardCarousel v-if="suggestedback" :results = "suggested"/>
    <CardCarousel v-if="hotelsback" :results = "hotels" />
    <CardCarousel v-if="foodback" :results = "food"/>
    <CardCarousel v-if="experiencesback" :results = "experiences"/>
  </div>
</template>

<script>
    import store from '../store'
    import axios from 'axios'
    import CardCarousel from '../components/CardCarousel'
    export default {
        components: {
            CardCarousel
        },

        data(){
            return {
                hotels: store.state.hotelResults,
                suggested: store.state.suggestedResults,
                food: store.state.foodResults,
                experiences: store.state.experiencesResults,
                suggestedback: false,
                hotelsback: false,
                foodback: false,
                experiencesback: false,
            }
        },

        mounted() {
                axios
                    .get('/yelpList/'+ store.state.location + "/4")
                    .then(res => {
                        store.commit('changeSuggestedResults', res.data.businesses);
                        this.suggestedback = true;
                        console.log("Done");
                    }).catch(err => {
                        console.log(err)
                });
               axios
                    .get('/yelpList/'+ store.state.location + "/1")
                    .then(res => {
                        store.commit('changeExperiencesResults', res.data.businesses);
                        this.experiencesback = true;
                        console.log("Done");
                    }).catch(err => {
                    console.log(err)
                });
               axios
                    .get('/yelpList/' + store.state.location + "/2")
                    .then(res => {
                        store.commit('changeFoodResults', res.data.businesses);
                        this.foodback = true;
                        console.log("Done");
                    }).catch(err => {
                    console.log(err)
                });
                axios
                    .get('/yelpList/' + store.state.location + "/3")
                    .then(res => {
                        store.commit('changeHotelResults', res.data.businesses);
                        this.hotelback = true;
                        console.log("Done");
                    }).catch(err => {
                    console.log(err)
                });
        }
    }
</script>